import { Fragment, useEffect, useState } from 'react';
import {
  Link,
  useParams,
} from 'react-router-dom';
import Row from 'react-bootstrap/Row';
import Spinner from 'react-bootstrap/Spinner';
import Identicon from '@polkadot/react-identicon';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
//import { Chart } from 'react-chartjs-2'
import { Line } from 'react-chartjs-2';
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

function Collator(props) {
  const { account } = useParams();
  const [history, setHistory] = useState(undefined);
  useEffect(() => {
    if (!!account && !history) {
      fetch(`https://81y8y0uwx8.execute-api.eu-central-1.amazonaws.com/prod/collator/${account}/history`)
        .then(response => response.json())
        .then((container) => {
          if (!!container.error) {
            console.error(container.error);
          } else {
            container.rounds.pop(); // lose the currently running round
            const rounds = container.rounds.filter(r => !!r.score); // lose rounds not participated in
            const score = Math.round(rounds.map((r) => r.score).reduce((acc, e) => acc + e, 0) / rounds.length);
            setHistory({ rounds, score });
          }
        })
        .catch((error) => {
          console.error(error);
        });
    }
  }, [account]);
  return (
    <Fragment>
      <Row>
        <h2>
          <Identicon value={account} size={24} theme={`substrate`} />
          <code style={{marginLeft: '0.5em'}}>
            { account }
          </code>
        </h2>
      </Row>
      {
        (!!history)
          ? (
              <Row style={{color: '#ffffff'}}>
                <h3>
                  overall productivity score <span className="text-muted">(round {history.rounds.slice(-1)[0].round} - {history.rounds[0].round})</span>: <span>{history.score}&#37;</span>
                </h3>
              </Row>
            )
          : null
      }
      {
        (!!history)
          ? (
              <Fragment>
                <Line
                  data={{
                    labels: history.rounds.map((r) => r.round),
                    datasets: [
                      {
                        label: account,
                        data: history.rounds.map((r) => r.score),
                        fill: true,
                        backgroundColor: '#d048b6',
                        borderColor: '#d048b6',
                        borderWidth: 2,
                        lineTension: 0.75,
                        pointBackgroundColor: '#d048b6',
                        pointBorderColor: 'rgba(255,255,255,0)',
                        pointHoverBackgroundColor: '#d048b6',
                        pointBorderWidth: 20,
                        pointHoverRadius: 4,
                        pointHoverBorderWidth: 15,
                        pointRadius: 4,
                      },
                    ],
                  }}
                />
                {
                  history.rounds.map((round) => (
                    <Row key={round.round} style={{color: '#ffffff'}}>
                      <h4>round { round.round }</h4>
                      <p>
                        productivity score: { round.score }&#37; <sup className="text-muted">{ round.authored } / { round.target }</sup>
                      </p>
                    </Row>
                  ))
                }
              </Fragment>
            )
          : (
              <Row className="d-flex align-items-center justify-content-center" style={{width: '100%', height: '600px'}}>
                <Spinner animation="border" variant="light">
                  <span className="visually-hidden">collator history lookup in progress...</span>
                </Spinner>
              </Row>
            )
      }
    </Fragment>
  );
}

export default Collator;
